<template>
    <div>
        <top-nav title="订单详情"></top-nav>
        <div class="wyu-main" v-loading="loading">
            <div style="min-width: 1600px">
                <div class="wyu-block">
                    <vue-easy-print tableShow ref="easyPrint">
                        <template>
                            <el-row :gutter="10"
                                    style="padding: 10px;width: 90%;margin: 0 auto;border: 1px solid #f2f2f2">
                                <el-col :span="24">
                                    <div class="wyu-title" style="position: revert">
                                        <h2>基本信息</h2>
                                        <div style="position: absolute;right: 10px;top: 16px">
                                            <el-button size="mini" icon="el-icon-printer" @click="printDemo">打印订单
                                            </el-button>
                                        </div>
                                    </div>
                                    <el-form label-position="left" inline style="padding: 0" class="demo-table-expand">
                                        <el-form-item label="订单 ID：">
                                            <span>{{detail.order_id}}</span>
                                        </el-form-item>
                                        <el-form-item label="订单号：">
                                            <span>{{detail.order_sn}}</span>
                                        </el-form-item>
                                        <el-form-item label="会员：">
                                            <span>{{detail.user.nickname}}(ID:{{detail.user.user_id}})</span>
                                        </el-form-item>
                                        <el-form-item label="电话：">
                                            <span>{{detail.user.mobile}}</span>
                                        </el-form-item>
                                        <el-form-item label="应付金额：">
                                            <span class="wyu-color-red">￥：{{detail.payment_money}}</span>
                                        </el-form-item>
                                        <el-form-item label="订单状态：">
                                            <span>{{detail.order_status_mean}}</span>
                                        </el-form-item>
                                        <el-form-item label="支付状态：">
                                            <span>{{detail.pay_status_mean}}</span>
                                        </el-form-item>
                                        <el-form-item label="发货状态：">
                                            <span>{{detail.shipping_status_mean}}</span>
                                        </el-form-item>
                                        <el-form-item label="下单时间：">
                                            <span>{{detail.add_time}}</span>
                                        </el-form-item>
                                        <el-form-item label="支付时间：">
                                            <span>{{detail.pay_time}}</span>
                                        </el-form-item>
                                        <el-form-item label="支付方式：">
                                            <span>{{detail.pay_type}}</span>
                                        </el-form-item>
                                        <el-form-item label="购买身份：">
                                            <span>{{detail.buy_role == 1  ? 'PLUS会员' : '注册会员'}}</span>
                                        </el-form-item>
                                    </el-form>
                                </el-col>

                                <el-col :span="24">
                                    <div class="wyu-title">
                                        <h2>收货信息</h2>
                                    </div>
                                    <el-form label-position="left" inline style="padding: 0" class="demo-table-expand">
                                        <el-form-item label="收货人：" style="width: 10%">
                                            <span>{{detail.name}}</span>
                                        </el-form-item>
                                        <el-form-item label="联系方式：" style="width: 15%">
                                            <span>{{detail.mobile}}</span>
                                        </el-form-item>
                                        <el-form-item label="收货地址：" style="width: 50%">
                                            <span>{{detail.address}}</span>
                                        </el-form-item>
                                        <el-form-item label="配送方式：" style="width: 20%">
                                            <span>{{detail.shipping_name}}</span>
                                            <el-button v-if="detail.shipping_status != 0" @click="guijiDialog = true"
                                                       type="text" style="margin-left: 10px">轨迹
                                            </el-button>
                                        </el-form-item>
                                        <el-form-item label="留言：" style="width: 100%">
                                            <span style="font-size: 12px;">
                                                {{detail.user_note == '' ? '无' : detail.user_note}}
                                            </span>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                                <el-col :span="24">
                                    <div class="wyu-title">
                                        <h2>商品信息</h2>
                                    </div>
                                    <el-table size="mini" :data="goods" border style="width: 100%">
                                        <el-table-column prop="goods_sn" label="商品编号" width="120"></el-table-column>
                                        <el-table-column prop="bar_code" label="SKU编码" width="120"></el-table-column>
                                        <el-table-column prop="name" label="缩略图" width="70">
                                            <template slot-scope="scope">
                                                <img width="50" height="50" :src="scope.row.goods_photo">
                                            </template>
                                        </el-table-column>
                                        <el-table-column prop="address" label="商品">
                                            <template slot-scope="scope">
                                                <div class="ellipsis-02">
                                                    {{scope.row.goods_name}}
                                                </div>
                                            </template>
                                        </el-table-column>
                                        <el-table-column prop="address" label="规格">
                                            <template slot-scope="scope">
                                                <span v-if="scope.row.sku_type == 1">
                                                    <p v-for="i in scope.row.spec_key_name"><b>{{i.key}}：</b>{{i.value}}</p>
                                                </span>
                                                <p v-if="scope.row.sku_type == 0">无规格</p>
                                            </template>
                                        </el-table-column>
                                        <el-table-column label="数量" width="50">
                                            <template slot-scope="scope">
                                                X{{scope.row.goods_num}}
                                            </template>
                                        </el-table-column>
                                        <el-table-column prop="address" label="购买价格" width="120">
                                            <template slot-scope="scope">
                                                <span class="wyu-color-red">￥{{scope.row.goods_price}}</span>
                                            </template>
                                        </el-table-column>
                                        <el-table-column prop="address" label="购买VIP价格" width="120">
                                            <template slot-scope="scope">
                                                <span class="wyu-color-red">￥{{scope.row.vip_price}}</span>
                                            </template>
                                        </el-table-column>
                                        <el-table-column prop="address" label="小计" width="120">
                                            <template slot-scope="scope">
                                                <span v-if="detail.buy_role == 0" class="wyu-color-red">￥{{scope.row.count}}</span>
                                                <span v-if="detail.buy_role == 1" class="wyu-color-red">￥{{scope.row.vip_count}}</span>
                                            </template>
                                        </el-table-column>
                                    </el-table>
                                </el-col>
                                <el-col :span="24">
                                    <div class="wyu-title">
                                        <h2>费用信息</h2>
                                    </div>
                                    <el-form label-position="left" inline style="padding: 0" class="demo-table-expand">
                                        <el-form-item label="小计">
                                            <span class="wyu-color-red">￥：{{detail.goods_price}}</span>
                                        </el-form-item>
                                        <el-form-item label="运费">
                                            <span class="wyu-color-red">￥：{{detail.shipping_price}}</span>
                                        </el-form-item>
                                        <el-form-item label="余额抵扣">
                                            <span class="wyu-color-red">￥：{{detail.user_money}}</span>
                                        </el-form-item>
                                        <el-form-item label="优惠券抵扣">
                                            <span class="wyu-color-red">￥：{{detail.coupon_price}}</span>
                                        </el-form-item>
                                        <el-form-item label="促销">
                                            <span class="wyu-color-red">￥：{{detail.youhui_price}}</span>
                                            <!--                                            <el-button type="text" style="margin-left: 10px">促销明细</el-button>-->
                                        </el-form-item>
                                        <el-form-item label="应付">
                                            <span class="wyu-color-red">￥：{{detail.payment_money}}</span>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                                <el-col :span="24" v-if="agent.is_commission == 1">
                                    <div class="wyu-title">
                                        <h2>分佣信息</h2>
                                    </div>
                                    <el-form label-position="left" inline style="padding: 0" class="demo-table-expand">
                                        <el-form-item label="佣金总计">
                                            <span class="wyu-color-red">￥：{{detail.commission}}</span>
                                            <el-button
                                                    v-if="detail.is_distribut == 0"
                                                    style="margin-left: 20px"
                                                    @click="commission"
                                                    size="mini">确认分佣
                                            </el-button>
                                        </el-form-item>
                                        <el-form-item style="width: 100%" label="直接推广者：" v-if="agent.agent.pid">
                                            <p><img width="20"
                                                    :src="agent.agent.pid.face"/>
                                                {{agent.agent.pid.nickname}}（ID：{{agent.agent.pid.user_id}}
                                                <router-link target="_blank"
                                                             :to="'/agent/manage/view/' +agent.agent.pid.user_id ">
                                                    <el-button type="text">跟踪</el-button>
                                                </router-link>
                                                ）
                                                可获得<span class="wyu-color-red wyu-fenyong-item">￥：{{agent.agent.pid.money}}</span>
                                                当前提成:<span class="wyu-color-red wyu-fenyong-item">{{agent.agent.pid.now_rate}}%</span>
                                                顾问级别:<span class="wyu-color-red wyu-fenyong-item">{{agent.agent.pid.agent_mean}}</span>
                                            </p>
                                        </el-form-item>
                                        <el-form-item style="width: 100%" label="推广者父级：" v-if="agent.agent.pid2">
                                            <p><img width="20"
                                                    :src="agent.agent.pid2.face"/>
                                                {{agent.agent.pid2.nickname}}（ID：{{agent.agent.pid2.user_id}}
                                                <router-link target="_blank"
                                                             :to="'/agent/manage/view/' +agent.agent.pid2.user_id ">
                                                    <el-button type="text">跟踪</el-button>
                                                </router-link>
                                                ）
                                                可获得<span class="wyu-color-red wyu-fenyong-item">￥：{{agent.agent.pid2.money}}</span>
                                                当前提成:<span class="wyu-color-red wyu-fenyong-item">{{agent.agent.pid2.now_rate}}%</span>
                                                顾问级别:<span class="wyu-color-red wyu-fenyong-item">{{agent.agent.pid2.agent_mean}}</span>
                                            </p>
                                        </el-form-item>
                                        <el-form-item style="width: 100%" label="推广者曾级：" v-if="agent.agent.pid3">
                                            <p><img width="20"
                                                    :src="agent.agent.pid3.face"/>
                                                {{agent.agent.pid3.nickname}}（ID：{{agent.agent.pid3.user_id}}
                                                <router-link target="_blank"
                                                             :to="'/agent/manage/view/' +agent.agent.pid3.user_id ">
                                                    <el-button type="text">跟踪</el-button>
                                                </router-link>
                                                ）
                                                可获得<span class="wyu-color-red wyu-fenyong-item">￥：{{agent.agent.pid3.money}}</span>
                                                当前提成:<span class="wyu-color-red wyu-fenyong-item">{{agent.agent.pid3.now_rate}}%</span>
                                                顾问级别:<span class="wyu-color-red wyu-fenyong-item">{{agent.agent.pid3.agent_mean}}</span>
                                            </p>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                                <el-col :span="24">
                                    <div class="wyu-title">
                                        <h2>操作信息</h2>
                                    </div>
                                    <el-form label-position="left" inline style="padding: 0" class="demo-table-expand">
                                        <el-form-item style="width: 100%" label="操作备注">
                                            <span>{{detail.admin_note}}</span>
                                            <el-button @click="noteDialog = true" type="text" style="margin-left: 10px">
                                                修改
                                            </el-button>
                                        </el-form-item>
                                        <el-form-item label="可操作项目" style="width: 100%">
                                            <router-link
                                                    v-if="detail.order_status == 1 && detail.pay_status == 1 && detail.shipping_status == 0"
                                                    style="margin-right: 10px"
                                                    :to="'/delivery/order/view/'+detail.order_id">
                                                <el-button size="mini">去发货</el-button>
                                            </router-link>
                                            <el-button
                                                    @click="setYesOrder"
                                                    v-if="detail.order_status == 0 && detail.pay_status == 1 && detail.shipping_status == 0"
                                                    size="mini">确认订单
                                            </el-button>
                                            <el-button
                                                    @click="setNoOrder"
                                                    v-if="detail.order_status == 1 && detail.pay_status == 1 && detail.shipping_status == 0"
                                                    size="mini">取消确认
                                            </el-button>
                                            <el-button
                                                    @click="setNoPay"
                                                    v-if="detail.order_status == 0 && detail.pay_status == 1 && detail.shipping_status == 0"
                                                    size="mini">设为未付款
                                            </el-button>
                                            <el-button
                                                    @click="setYesPay"
                                                    v-if="detail.order_status == 0 && detail.pay_status == 0 && detail.shipping_status == 0"
                                                    size="mini">设为已付款
                                            </el-button>
                                            <el-button @click="setInvalid" size="mini" v-if="detail.order_status != 5">
                                                无效
                                            </el-button>
                                            <el-button @click="deleteAdmin" v-if="detail.order_status == 5" size="mini">
                                                删除订单
                                            </el-button>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                            </el-row>
                        </template>
                    </vue-easy-print>
                </div>
            </div>
        </div>

        <!--备注操作弹窗-->
        <el-dialog title="管理员备注" :visible.sync="noteDialog" width="500px" center
                   :modal-append-to-body="false">
            <el-form size="mini" style="padding: 0" v-loading="noteLoading">
                <el-form-item>
                    <el-input type="textarea"
                              :rows="5" v-model="detail.admin_note"></el-input>
                </el-form-item>
                <el-form-item style="text-align: right">
                    <el-button size="mini" @click="noteDialog = false">取 消</el-button>
                    <el-button size="mini" type="primary" @click="setNote">确 定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <!--轨迹弹窗-->
        <el-dialog title="轨迹" :visible.sync="guijiDialog" width="600px" center
                   :modal-append-to-body="false">
            <el-row>
                <el-col :span="24" style="margin-bottom: 20px">
                    <span style="font-size: 18px"> {{detail.shipping_name}} : {{detail.shipping_num}}</span>
                    <el-button type="text" style="margin-left:10px;" class="tag-read" @click="copy"
                               :data-clipboard-text="detail.shipping_name+':'+detail.shipping_num"
                               href="javascript:void(0);">复制
                    </el-button>
                </el-col>
                <el-col v-if="trail.is_open == 0" :span="24" style="text-align: center">
                    <h1>您未配置快递信息</h1>
                </el-col>
                <el-col v-if="trail.is_open == 1" :span="24"
                        style="max-height: 500px;overflow: hidden;overflow-y: auto">
                    <h1 v-if="trail.data.length <= 0">未查询到物流信息，请确认物流已发出、物流单号、以及快递信息配置。</h1>
                    <el-timeline>
                        <el-timeline-item
                                v-for="(activity, index) in trail.data"
                                :key="index"
                                :type="index == 0 ? 'primary' : ''"
                                :timestamp="activity.AcceptTime">
                            {{activity.AcceptStation}}
                        </el-timeline-item>
                    </el-timeline>
                </el-col>
            </el-row>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from '@/api.js';
    import vueEasyPrint from "vue-easy-print";
    import TopNav from "@/components/TopNav";
    import Clipboard from 'clipboard';

    export default {
        name: "travelDepart",
        components: {TopNav, vueEasyPrint},
        data: function () {
            return {
                loading: false,
                noteLoading: false,
                noteDialog: false,
                guijiDialog: false,
                trail: {},
                detail: {
                    user: {},

                },
                agent: {is_commission: 0, agent: []},
                goods: [],
            };
        },
        created() {
            this.getOrderDetail();
        },
        methods: {
            copy() {
                var clipboard = new Clipboard('.tag-read');
                clipboard.on('success', () => {
                    this.$message({
                        message: '复制成功',
                        type: 'success'
                    });
                    clipboard.destroy()
                });
                clipboard.on('error', () => {
                    this.$message({
                        message: '您的浏览器 不支持，请手动复制链接',
                        type: 'error'
                    });
                    clipboard.destroy()
                })
            },
            setNote() {
                this.noteLoading = true;
                api.post("/shop/admin.order.goodsorder/adminNote", {
                    appToken: this.$store.getters.appToken,
                    note: this.detail.admin_note,
                    order_id: this.$route.params.id,
                }).then(() => {
                    this.noteLoading = false;
                    this.noteDialog = false;
                }).catch(() => {
                    this.noteLoading = false;
                });
            },

            //确认分佣
            commission() {
                this.$confirm('确认为此订单分佣吗？', '温馨提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.post("/shop/admin.order.goodsorder/commission", {
                        appToken: this.$store.getters.appToken,
                        order_id: this.$route.params.id,
                    }).then(() => {
                        this.getOrderDetail();
                    }).catch(() => {
                    });
                }).catch(() => {
                });
            },
            //确认订单
            setYesOrder() {
                this.$confirm('确认此订单吗？', '温馨提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.post("/shop/admin.order.goodsorder/setYesOrder", {
                        appToken: this.$store.getters.appToken,
                        order_id: this.$route.params.id,
                    }).then(() => {
                        this.getOrderDetail();
                    }).catch(() => {
                    });
                }).catch(() => {
                });
            },
            deleteAdmin() {
                this.$confirm('确认此订单吗？', '温馨提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.post("/shop/admin.order.goodsorder/delete", {
                        appToken: this.$store.getters.appToken,
                        order_id: this.$route.params.id,
                    }).then(() => {
                        this.$router.push("/order/product/index")
                    }).catch(() => {
                    });
                }).catch(() => {
                });
            },

            //取消确认订单
            setNoOrder() {
                this.$confirm('确认取消此订单吗？', '温馨提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.post("/shop/admin.order.goodsorder/setNoOrder", {
                        appToken: this.$store.getters.appToken,
                        order_id: this.$route.params.id,
                    }).then(() => {
                        this.getOrderDetail();
                    }).catch(() => {
                    });
                }).catch(() => {
                });
            },
            //取消确认订单
            setYesPay() {
                this.$confirm('确认标记此订单为已付款吗？', '温馨提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.post("/shop/admin.order.goodsorder/setYesPay", {
                        appToken: this.$store.getters.appToken,
                        order_id: this.$route.params.id,
                    }).then(() => {
                        this.getOrderDetail();
                    }).catch(() => {
                    });
                }).catch(() => {
                });
            },
            //设置为未支付
            setNoPay() {
                this.$confirm('确认标记此订单为未付款吗？', '温馨提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.post("/shop/admin.order.goodsorder/setNoPay", {
                        appToken: this.$store.getters.appToken,
                        order_id: this.$route.params.id,
                    }).then(() => {
                        this.getOrderDetail();
                    }).catch(() => {
                    });
                }).catch(() => {
                });
            },
            //设置为未支付
            setInvalid() {
                this.$confirm('确认无效此订单吗？', '温馨提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.post("/shop/admin.order.goodsorder/setInvalid", {
                        appToken: this.$store.getters.appToken,
                        order_id: this.$route.params.id,
                    }).then(() => {
                        this.getOrderDetail();
                    }).catch(() => {
                    });
                }).catch(() => {
                });
            },
            printDemo() {
                this.$refs.easyPrint.print()
            },
            getOrderDetail() {
                this.loading = true;
                api.post("/shop/admin.order.goodsorder/detail", {
                    appToken: this.$store.getters.appToken,
                    order_id: this.$route.params.id,
                }).then(res => {
                    this.detail = res.detail;
                    this.goods = res.goods;
                    this.trail = res.trail;
                    this.agent = res.agent;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            }
        }
    };
</script>
<style scoped>

    .demo-table-expand {
        font-size: 0;
    }

    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 25%;
    }

    .el-timeline-item {
        list-style: none;
    }

    .wyu-fenyong-item {
        display: inline-block;
        margin: 0 6px;
        font-size: 16px;
    }
</style>
